/**
 * @description 
 * @param {Array} arr : 
 * @return {any} 
 */
export const randomChoice = (arr) => {
  return arr[Math.floor(Math.random() * arr.length)];
}

/**
 * @description 
 * @param {String} datetime : 2019-07-16 11:11:11
 * @return {String} 
 */
export const timeToAgo = (datetime) => {
  const diffTime =
    new Date().getTime() -
    new Date(datetime.replace(/\-/g, "/")).getTime();

  if (diffTime < 0) {
    console.log(`${datetime}`);
    return datetime;
  }

  const dayDiff = diffTime / (86400000);
  const hourDiff = diffTime / (3600000);
  const minDiff = diffTime / (60000);

  if (dayDiff > 3) {
    return datetime;
  } else if (dayDiff >= 1 && dayDiff <= 3) {
    return parseInt(dayDiff) + "";
  } else if (hourDiff >= 1) {
    return parseInt(hourDiff) + "";
  } else if (minDiff >= 1) {
    return parseInt(minDiff) + "";
  } else {
    return "";
  }
}